import React, { useContext, useEffect, useRef,useState } from "react";
import './navbar.css'
import { useHistory } from "react-router";
import noteContext from "../context/notes/noteContext";
import AddNote from "./AddNote";
import NoteItem from "./NoteItem";
import DarkModeContext from "../context/dark mode/darkModeContext";
const Notes = (props) => {
  const context = useContext(noteContext);
  const { notes, getAllNotes,editNote } = context;
  const [note, setNote] = useState({id: "", etitle: "", edescription: "", etag: ""})
  const refClose=useRef(null);
  const ref = useRef(null); // this is used to refer a element
  let history = useHistory();
  const darkModeContext = useContext(DarkModeContext);
  const { darkMode} = darkModeContext;
  
  const updateNote = (currentNote) => {
    ref.current.click();
    setNote({id: currentNote._id, etitle: currentNote.title, edescription: currentNote.discription, etag:currentNote.tag})
    
  };
  const handleClick=(e)=>{
    e.preventDefault();
    editNote(note.id,note.etitle,note.edescription,note.etag)
    refClose.current.click();
    props.showAlert("Update Successfully","success"); 
  }

  const onChange=(e)=>{
    setNote({...note,[e.target.name]:e.target.value}); //all properties are same but those prperty that specified after "," will add is not present or update
  }

  

  useEffect(() => {
    if(localStorage.getItem('token')){
      getAllNotes("");
    }
    else{
      history.push("/login_signup");
    }

    // eslint-disable-next-line
  }, []);

  return (
    <>
      <AddNote showAlert={props.showAlert}/>
      <button
        ref={ref}
        type="button"
        className="btn btn-primary d-none"
        data-bs-toggle="modal"
        data-bs-target="#exampleModal"
      >
      </button>
      <div
        className="modal fade"
        id="exampleModal"
        tabIndex="-1"
        aria-labelledby="exampleModalLabel"
        aria-hidden="true"
      >
        <div className="modal-dialog">
          <div className="modal-content">
            <div className="modal-header">
              <h5 className="modal-title" id="exampleModalLabel">
                Edit Note
              </h5>
              <button
                type="button"
                className="btn-close"
                data-bs-dismiss="modal"
                aria-label="Close"
              ></button>
            </div>
            <div className="modal-body">
              <form className="my-3">
                <div className="mb-3">
                  <label htmlFor="title" className="form-label">
                    Title
                  </label>
                  <input
                    type="text"
                    className="form-control"
                    id="etitle"
                    name="etitle"
                    value={note.etitle}
                    aria-describedby="emailHelp"
                    onChange={onChange}
                    minLength={5} required
                  />
                </div>
                <div className="mb-3">
                  <label htmlFor="description" className="form-label">
                    Description
                  </label>
                  <input
                    type="text"
                    className="form-control"
                    id="edescription"
                    name="edescription"
                    value={note.edescription}
                    onChange={onChange}
                    minLength={5}
                    required
                  />
                </div>
                <div className="mb-3">
                  <label htmlFor="tag" className="form-label">
                    Tag
                  </label>
                  <input
                    type="text"
                    className="form-control"
                    id="etag"
                    name="etag"
                    value={note.etag}
                    onChange={onChange}
                  />
                </div>
              </form>
            </div>
            <div className="modal-footer">
              <button
                ref={refClose}
                type="button"
                className="btn btn-cancel btn-secondary"
                data-bs-dismiss="modal"
              >
                Close
              </button>
              <button
                disabled={
                  note.etitle.length < 5 || note.edescription.length < 5
                }
                onClick={handleClick}
                type="button"
                className="btn btn-primary"
              > 
                Update Note
              </button>
            </div>
          </div>
        </div>
      </div>
      <div className="row my-3">
        <h1 className={`text-${(darkMode==="light")? "dark":"light"}`}>Your Note</h1>
        {notes.length===0?<h5 className={`text-${(darkMode==="light")? "dark":"light"}`}>No Notes To Display</h5>:null}
        {notes.map((note) => {
          return (
            <NoteItem key={note._id} updateNote={updateNote} showAlert={props.showAlert} note={note} />
          );
        })}
      </div>
    </>
  );
};

export default Notes;
